/**
 * Preloader Styles
 *
 * @package emptytheme
 */
/* Preloader Container */
#preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #1e3c72 0%, #2a5298 100%);
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 1;
  visibility: visible;
  transition: all 0.5s ease-in-out;
}

#preloader.hidden {
  opacity: 0;
  visibility: hidden;
}

#preloader.removed {
  display: none;
}

/* Preloader Content */
.preloader-content {
  text-align: center;
  color: white;
  font-family: var(--font-urbanist, sans-serif);
  font-size: 1.2rem;
  font-weight: 500;
}

/* Spinner Styles */
.preloader-spinner {
  width: 80px;
  height: 80px;
  margin: 0 auto 30px;
  position: relative;
  border-radius: 50%;
  background: conic-gradient(from 0deg, transparent, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.3), rgba(255, 255, 255, 0.6), white, transparent);
  animation: spin 1.2s linear infinite;
}

.preloader-spinner::before {
  content: "";
  position: absolute;
  top: 8px;
  left: 8px;
  right: 8px;
  bottom: 8px;
  background: linear-gradient(135deg, #1e3c72 0%, #2a5298 100%);
  border-radius: 50%;
  z-index: 1;
}

.preloader-spinner::after {
  content: "";
  position: absolute;
  top: 20px;
  left: 20px;
  right: 20px;
  bottom: 20px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  z-index: 2;
  animation: pulse-inner 2s ease-in-out infinite;
}

/* Text Styles */
.preloader-text {
  margin-top: 20px;
  opacity: 0.9;
  animation: pulse 2s ease-in-out infinite;
}

/* Progress Bar */
.preloader-progress {
  width: 200px;
  height: 4px;
  background: rgba(255, 255, 255, 0.3);
  border-radius: 2px;
  margin: 20px auto 0;
  overflow: hidden;
}

.preloader-progress-bar {
  height: 100%;
  background: white;
  border-radius: 2px;
  width: 0%;
  animation: progress 3s ease-in-out infinite;
}

/* Animations */
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes pulse {
  0%, 100% {
    opacity: 0.9;
  }
  50% {
    opacity: 0.6;
  }
}
@keyframes pulse-inner {
  0%, 100% {
    opacity: 0.1;
    transform: scale(1);
  }
  50% {
    opacity: 0.3;
    transform: scale(1.1);
  }
}
@keyframes progress {
  0% {
    width: 0%;
  }
  50% {
    width: 70%;
  }
  100% {
    width: 100%;
  }
}
/* Responsive Design */
@media (max-width: 768px) {
  .preloader-spinner {
    width: 60px;
    height: 60px;
    margin-bottom: 20px;
  }
  .preloader-spinner::before {
    top: 6px;
    left: 6px;
    right: 6px;
    bottom: 6px;
  }
  .preloader-spinner::after {
    top: 15px;
    left: 15px;
    right: 15px;
    bottom: 15px;
  }
  .preloader-content {
    font-size: 1rem;
  }
  .preloader-progress {
    width: 150px;
  }
}
.header-burger {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 4rem;
  margin-left: 1.6rem;
  border: none;
  background-color: transparent;
  color: var(--color-white);
  cursor: pointer;
}

.header-burger-icon {
  position: relative;
  display: block;
  width: 2.4rem;
  height: 1.8rem;
  margin: auto;
}

.header-burger-icon i {
  position: absolute;
  right: 0;
  display: block;
  width: inherit;
  height: 0.1rem;
  background-color: var(--color-white);
  border-radius: 0.4rem;
  transition: top 0.35s ease-in-out, background-color 0.35s ease;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  perspective: 1000;
  transform: translateZ(0);
}

.header-burger-icon i:first-child {
  top: 0;
}

.header-burger-icon i:nth-child(2) {
  top: 0.8rem;
  width: 55%;
}

.header-burger-icon i:last-child {
  top: 1.6rem;
  width: 80%;
}

.header-burger.active i:first-child {
  opacity: 1;
  top: 0.8rem;
  width: 75%;
  transition: top 0.35s ease, width 0.35s ease 0.35s, transform 0.5s ease 0.35s, background-color 0.35s ease;
  transform: rotate(45deg);
}

.header-burger.active i:nth-child(2) {
  opacity: 0;
  transition: opacity 50ms ease 0.35s;
}

.header-burger.active i:last-child {
  opacity: 1;
  top: 0.8rem;
  width: 75%;
  transition: top 0.35s ease, width 0.35s ease 0.35s, transform 0.5s ease 0.35s, background-color 0.35s ease;
  transform: rotate(-45deg);
}

