/* Mobile Menu Overlay */
#mobile-menu-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.85);
  z-index: 998;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease, visibility 0.3s ease;
}
#mobile-menu-overlay.active {
  opacity: 1;
  visibility: visible;
}
body.admin-bar #mobile-menu-overlay {
  top: 32px;
  height: calc(100vh - 32px);
}
@media (max-width: 782.98px) {
  body.admin-bar #mobile-menu-overlay {
    top: 46px;
    height: calc(100vh - 46px);
  }
}
@media (max-width: 767.98px) {
  #mobile-menu-overlay {
    display: none !important;
  }
}

/* Mobile Menu */
#mobile-menu {
  position: fixed;
  top: 0;
  right: 0;
  width: 80%;
  max-width: 32rem;
  height: 100vh;
  background: linear-gradient(132deg, #2c2c2c 0%, #3e3e3e 50%, #2c2c2c 100%);
  z-index: 999;
  overflow-y: auto;
  transform: translateX(100%);
  transition: transform 0.3s ease;
  color: var(--color-white, #ffffff);
}
#mobile-menu::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 0.1875rem;
  background: linear-gradient(90deg, #000000 0%, #d8b150 25%, #666666 50%, #d5af51 79%, #191409 100%);
  z-index: 1;
}
#mobile-menu.active {
  transform: translateX(0);
}
body.admin-bar #mobile-menu {
  top: 32px;
  height: calc(100vh - 32px);
}
@media (max-width: 782.98px) {
  body.admin-bar #mobile-menu {
    top: 46px;
    height: calc(100vh - 46px);
  }
}
#mobile-menu .mobile-menu__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 1.6rem;
