}
#mobile-menu .mobile-menu__close svg,
#mobile-menu .mobile-menu__close .icon {
  width: 2.4rem;
  height: 2.4rem;
}
#mobile-menu .mobile-menu__nav {
  padding: 2rem 0;
}
#mobile-menu .mobile-menu__list {
  list-style: none;
  margin: 0;
  padding: 0;
}
#mobile-menu .mobile-menu__item,
#mobile-menu .menu-item {
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
}
#mobile-menu .mobile-menu__item:last-child,
#mobile-menu .menu-item:last-child {
  border-bottom: none;
}
#mobile-menu .mobile-menu__link,
#mobile-menu .menu-item a {
  display: block;
  padding: 1.6rem 1.6rem;
  color: var(--color-white, #ffffff);
  text-decoration: none;
  font-size: 1.6rem;
  font-weight: 500;
  text-align: center;
  transition: background-color 0.3s ease, color 0.3s ease, padding-left 0.3s ease;
}
#mobile-menu .mobile-menu__link:hover, #mobile-menu .mobile-menu__link:focus,
#mobile-menu .menu-item a:hover,
