  background-color: rgba(255, 255, 255, 0.05);
  color: var(--color-yellow, #d8b150);
  padding-left: 2rem;
}
#mobile-menu .mobile-menu__link.active, #mobile-menu .mobile-menu__link.current-menu-item,
#mobile-menu .menu-item a.active,
#mobile-menu .menu-item a.current-menu-item {
  background-color: rgba(216, 177, 80, 0.1);
  color: var(--color-yellow, #d8b150);
}
#mobile-menu .current-menu-item > a {
  background-color: rgba(216, 177, 80, 0.1);
  color: var(--color-yellow, #d8b150);
}
#mobile-menu .mobile-menu__footer {
