/* 404 Error Page Styles */
.error-404-page {
  min-height: 100vh;
  display: flex;
  align-items: center;
  background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
  position: relative;
  overflow: hidden;
}
.error-404-page::before {
  content: "";
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle, rgba(216, 177, 80, 0.1) 0%, transparent 70%);
  animation: float 20s ease-in-out infinite;
  z-index: 0;
}
.error-404-page::after {
  content: "";
  position: absolute;
  top: 20%;
  right: -10%;
  width: 300px;
  height: 300px;
  background: radial-gradient(circle, rgba(31, 31, 31, 0.05) 0%, transparent 70%);
  border-radius: 50%;
  animation: float 15s ease-in-out infinite reverse;
  z-index: 0;
}

.error-404 {
  position: relative;
  z-index: 1;
  text-align: center;
  padding: 4rem 0;
}

.error-404__number {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  margin-bottom: 3rem;
  perspective: 1000px;
}
@media (max-width: 767.98px) {
  .error-404__number {
    gap: 1rem;
    margin-bottom: 2rem;
  }
}

.error-404__digit {
  font-family: var(--font-urbanist);
  font-size: 8rem;
  font-weight: 700;
  color: var(--color-dark);
  text-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transform-style: preserve-3d;
  animation: bounceIn 1s ease-out;
}
@media (max-width: 767.98px) {
  .error-404__digit {
    font-size: 5rem;
  }
}
@media (max-width: 575.98px) {
  .error-404__digit {
    font-size: 3.5rem;
  }
}
.error-404__digit--4 {
  animation-delay: 0.1s;
  background: linear-gradient(45deg, var(--color-yellow) 0%, #f4d03f 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.error-404__digit--0 {
  animation-delay: 0.2s;
  background: linear-gradient(45deg, var(--color-dark) 0%, #4a4a4a 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.error-404__digit--4-2 {
  animation-delay: 0.3s;
  background: linear-gradient(45deg, var(--color-yellow) 0%, #f4d03f 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.error-404__digit:hover {
  transform: rotateY(15deg) scale(1.1);
  transition: transform 0.3s ease;
}

.error-404__content {
  max-width: 800px;
  margin: 0 auto;
}

.error-404__header {
  margin-bottom: 3rem;
}

.error-404__title {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  color: var(--color-dark);
  animation: fadeInUp 0.8s ease-out 0.5s both;
}
@media (max-width: 767.98px) {
  .error-404__title {
    font-size: 2rem;
  }
}
@media (max-width: 575.98px) {
  .error-404__title {
    font-size: 1.5rem;
  }
}

.error-404__description {
  font-size: 1.25rem;
  color: var(--color-text);
  max-width: 600px;
  margin: 0 auto;
  animation: fadeInUp 0.8s ease-out 0.7s both;
}
@media (max-width: 767.98px) {
  .error-404__description {
    font-size: 1.125rem;
  }
}

.error-404__search {
  margin-bottom: 3rem;
  animation: fadeInUp 0.8s ease-out 0.9s both;
}
.error-404__search h3 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
  color: var(--color-dark);
  font-family: var(--font-urbanist);
  font-weight: 600;
  text-align: center;
}
.error-404__search .search-form {
  max-width: 500px;
  margin: 0 auto;
}
.error-404__search .search-suggestions {
  margin-top: 1.5rem;
  text-align: center;
}
.error-404__search .search-suggestions p {
  font-size: 0.95rem;
  color: var(--color-text);
  margin-bottom: 1rem;
  opacity: 0.8;
}
.error-404__search .search-suggestions .suggestion-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.75rem;
  justify-content: center;
}
.error-404__search .search-suggestions .suggestion-tags .tag {
  padding: 0.5rem 1rem;
  background: rgba(216, 177, 80, 0.1);
  border: 1px solid rgba(216, 177, 80, 0.3);
