  color: var(--color-dark);
  transform: translateY(-2px);
}

.error-404__actions {
  display: flex;
  gap: 1.5rem;
  justify-content: center;
  margin-bottom: 4rem;
  animation: fadeInUp 0.8s ease-out 1.1s both;
}
@media (max-width: 575.98px) {
  .error-404__actions {
    flex-direction: column;
    align-items: center;
    gap: 1rem;
  }
}
.error-404__actions .error-404__button {
  min-width: 180px;
  animation: pulse 2s infinite;
}
.error-404__actions .error-404__button:hover {
  animation: none;
  transform: translateY(-2px);
}

.error-404__helpful-links {
  animation: fadeInUp 0.8s ease-out 1.3s both;
}
.error-404__helpful-links h3 {
  font-size: 1.75rem;
  margin-bottom: 2rem;
  color: var(--color-dark);
