  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
}
@media (max-width: 767.98px) {
  .row {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
}

/* Post Cards for Archives */
.post-card {
  background: var(--color-white);
  overflow: hidden;
}

.post-card__wrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.post-card__image {
  position: relative;
  overflow: hidden;
}
.post-card__image::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, transparent 0%, rgba(0, 0, 0, 0.1) 100%);
  z-index: 1;
}

.post-card__thumb {
  width: 100%;
  height: 200px;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.post-card__placeholder {
  width: 100%;
  height: 200px;
  background: #f0f0f0;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #ccc;
}

.post-card__link {
  display: block;
  text-decoration: none;
  color: inherit;
}
.post-card__link:hover .post-card__thumb {
  transform: scale(1.1);
}

.post-card__content {
  padding: 1.5rem;
  flex: 1;
  display: flex;
  flex-direction: column;
}

.post-card__meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  font-size: 0.9rem;
}
@media (max-width: 575.98px) {
  .post-card__meta {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
  }
}

.post-card__date {
  color: var(--color-text);
  font-weight: 500;
}

.post-card__category {
  background: var(--color-yellow);
  color: var(--color-dark);
  padding: 0.25rem 0.75rem;
  border-radius: 15px;
  font-size: 0.8rem;
  font-weight: 500;
}

.post-card__title {
  margin-bottom: 1rem;
}
.post-card__title h3 {
  font-size: 1.25rem;
  line-height: 1.3;
  margin: 0;
}
.post-card__title h3 a {
  color: var(--color-dark);
  text-decoration: none;
  transition: color 0.3s ease;
}
.post-card__title h3 a:hover {
  color: var(--color-yellow);
}

.post-card__excerpt {
  color: var(--color-text);
  line-height: 1.6;
  margin-bottom: 1.5rem;
  flex: 1;
}

.post-card__footer {
  margin-top: auto;
}

.post-card__read-more {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  color: var(--color-yellow);
  font-weight: 500;
  text-decoration: none;
  transition: all 0.3s ease;
}
.post-card__read-more:hover {
  color: var(--color-dark);
  transform: translateX(5px);
}
.post-card__read-more svg {
  transition: transform 0.3s ease;
}
.post-card__read-more:hover svg {
  transform: translateX(3px);
}

/* Search Content Styles */
.search-content {
  margin-top: 3rem;
}

.search-query {
  color: var(--color-yellow);
  font-weight: 600;
}

.page-header {
  text-align: center;
  margin-bottom: 0;
  padding: 3rem 0;
  color: var(--color-text);
  position: relative;
}

.page-title {
  font-size: 3rem;
  margin-bottom: 1rem;
  position: relative;
  z-index: 1;
}
@media (max-width: 767.98px) {
  .page-title {
    font-size: 2rem;
  }
}

.archive-description {
  font-size: 1.125rem;
  opacity: 0.9;
  max-width: 600px;
  margin: 0 auto;
  position: relative;
  z-index: 1;
  line-height: 1.6;
}

/* Page Styles */
.page-content-wrapper {
  margin: 4rem 0;
}

.entry-excerpt {
  font-size: 1.25rem;
  opacity: 0.9;
  position: relative;
  z-index: 1;
  line-height: 1.6;
}

.page-featured-image {
  padding: 2rem 0;
  background: #f8f9fa;
}

.featured-image-wrapper {
  text-align: center;
}

.featured-image {
  max-width: 100%;
  height: auto;
  border-radius: 15px;
}

.entry-content {
  font-size: 1.125rem;
  line-height: 1.7;
  color: var(--color-text);
}
.entry-content h2, .entry-content h3, .entry-content h4, .entry-content h5, .entry-content h6 {
  margin-top: 2rem;
  margin-bottom: 1rem;
  color: var(--color-dark);
  font-family: var(--font-urbanist);
}
.entry-content h2 {
  font-size: 2rem;
  border-bottom: 2px solid var(--color-yellow);
  padding-bottom: 0.5rem;
}
.entry-content h3 {
  font-size: 1.5rem;
}
.entry-content p {
  margin-bottom: 1.5rem;
}
.entry-content ul, .entry-content ol {
  margin-bottom: 1.5rem;
  padding-left: 2rem;
}
.entry-content ul li, .entry-content ol li {
  margin-bottom: 0.5rem;
}
.entry-content blockquote {
  border-left: 4px solid var(--color-yellow);
  padding-left: 2rem;
  margin: 2rem 0;
  font-style: italic;
  background: #f8f9fa;
  padding: 1.5rem 2rem;
  border-radius: 0 10px 10px 0;
}
.entry-content img {
  border-radius: 10px;
  margin: 1.5rem 0;
}
.entry-content a {
  color: var(--color-yellow);
  text-decoration: underline;
  transition: color 0.3s ease;
}
.entry-content a:hover {
  color: var(--color-dark);
}

/* No Results Styles */
.no-results {
  text-align: center;
  padding: 4rem 2rem;
  background: var(--color-white);
  border-radius: 20px;
}

.no-results .page-header {
  background: none;
  padding: 0;
  margin-bottom: 2rem;
}

.no-results .page-title {
  color: var(--color-dark);
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

.page-content {
  font-size: 1.125rem;
  color: var(--color-text);
  line-height: 1.6;
}
.page-content p {
  margin-bottom: 2rem;
}

/* Navigation Styles */
.posts-pagination {
  margin-top: 3rem;
  display: flex;
  justify-content: center;
}
.posts-pagination .nav-links {
  display: flex;
  gap: 0.5rem;
  align-items: center;
  background: var(--color-white);
  padding: 1rem;
  border-radius: 25px;
}
.posts-pagination .nav-previous a,
.posts-pagination .nav-next a {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.25rem;
  background: var(--color-yellow);
  color: var(--color-dark);
  border-radius: 20px;
  text-decoration: none;
  font-weight: 500;
  font-size: 0.9rem;
  transition: all 0.3s ease;
}
.posts-pagination .nav-previous a:hover,
.posts-pagination .nav-next a:hover {
  background: var(--color-dark);
  color: var(--color-white);
  transform: translateY(-2px);
}
.posts-pagination .page-numbers {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  text-decoration: none;
  font-weight: 500;
  transition: all 0.3s ease;
}
.posts-pagination .page-numbers:not(.current) {
  color: var(--color-text);
}
.posts-pagination .page-numbers:not(.current):hover {
  background: var(--color-yellow);
  color: var(--color-dark);
}
.posts-pagination .page-numbers.current {
  background: var(--color-dark);
  color: var(--color-white);
}
.posts-pagination .page-numbers.dots {
  color: var(--color-text);
  cursor: default;
}
.posts-pagination .page-numbers.dots:hover {
  background: none;
  color: var(--color-text);
}

/* Archive Styles */
.archive-wrapper {
  margin: 4rem 0;
}

/* Comments Styles */
.comments-area {
  margin: 3rem 0;
  padding: 3rem;
  background: #f8f9fa;
}

.comments-title {
  font-size: 2.5rem;
  font-family: var(--font-urbanist);
  color: var(--color-dark);
  margin-bottom: 2rem;
  text-align: center;
  position: relative;
}
.comments-title::after {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 60px;
  height: 3px;
  background: var(--color-yellow);
  border-radius: 2px;
}
@media (max-width: 767.98px) {
  .comments-title {
    font-size: 2rem;
  }
}

/* Comment List */
.comment-list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.comment-list .comment {
  margin-bottom: 2rem;
  padding: 0;
  position: relative;
}
.comment-list .comment:last-child {
  margin-bottom: 0;
}

/* Individual Comment */
.comment-body {
  background: var(--color-white);
  border-radius: 15px;
  padding: 2rem;
  transition: all 0.3s ease;
  position: relative;
}
.comment-body:hover {
  transform: translateY(-2px);
}
.comment-body::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, var(--color-yellow) 0%, #f4d03f 100%);
  border-radius: 15px 15px 0 0;
}

/* Comment Meta */
.comment-meta {
  display: flex;
  align-items: center;
  margin-bottom: 1.5rem;
  gap: 1rem;
}
@media (max-width: 575.98px) {
  .comment-meta {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
  }
}

.comment-author {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}
.comment-author .avatar {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 3px solid var(--color-yellow);
  object-fit: cover;
  transition: all 0.3s ease;
}
.comment-author .avatar:hover {
  transform: scale(1.1);
  border-color: var(--color-dark);
}
.comment-author .fn {
  font-family: var(--font-urbanist);
  font-weight: 600;
  font-size: 1.125rem;
  color: var(--color-dark);
  text-decoration: none;
  transition: color 0.3s ease;
}
.comment-author .fn:hover {
  color: var(--color-yellow);
}
.comment-author .says {
  color: var(--color-text);
  font-weight: 400;
  margin-left: 0.25rem;
}

.comment-metadata {
  display: flex;
  align-items: center;
  gap: 1rem;
  font-size: 0.9rem;
  color: var(--color-text);
}
@media (max-width: 575.98px) {
  .comment-metadata {
    flex-wrap: wrap;
    gap: 0.5rem;
  }
}
.comment-metadata a {
  color: var(--color-text);
  text-decoration: none;
  transition: color 0.3s ease;
}
.comment-metadata a:hover {
  color: var(--color-yellow);
}
.comment-metadata .edit-link {
  background: var(--color-yellow);
  color: var(--color-dark);
  padding: 0.25rem 0.75rem;
  border-radius: 15px;
  font-weight: 500;
  font-size: 0.8rem;
  transition: all 0.3s ease;
}
.comment-metadata .edit-link:hover {
  background: var(--color-dark);
  color: var(--color-white);
  transform: translateY(-1px);
}

/* Comment Content */
.comment-content {
  font-size: 1rem;
  line-height: 1.7;
  color: var(--color-text);
  margin-bottom: 1.5rem;
}
.comment-content p {
  margin-bottom: 1rem;
}
.comment-content p:last-child {
  margin-bottom: 0;
}
.comment-content a {
  color: var(--color-yellow);
  text-decoration: underline;
  transition: color 0.3s ease;
}
.comment-content a:hover {
  color: var(--color-dark);
}
.comment-content code {
  background: #f1f3f4;
  padding: 0.25rem 0.5rem;
  border-radius: 4px;
  font-family: "Courier New", monospace;
  font-size: 0.9em;
  color: var(--color-dark);
}
.comment-content pre {
  background: #f1f3f4;
  padding: 1rem;
  border-radius: 8px;
  overflow-x: auto;
  margin: 1rem 0;
}
.comment-content pre code {
  background: none;
  padding: 0;
}
.comment-content blockquote {
  border-left: 4px solid var(--color-yellow);
  padding-left: 1.5rem;
  margin: 1rem 0;
  font-style: italic;
  background: rgba(216, 177, 80, 0.05);
  padding: 1rem 1.5rem;
  border-radius: 0 8px 8px 0;
}

/* Comment Reply Link */
.reply {
  text-align: right;
}
.reply a {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  background: var(--color-dark);
  color: var(--color-white);
  padding: 0.75rem 1.25rem;
  border-radius: 25px;
  text-decoration: none;
  font-weight: 500;
  font-size: 0.9rem;
  transition: all 0.3s ease;
}
.reply a:hover {
  background: var(--color-yellow);
  color: var(--color-dark);
  transform: translateY(-2px);
}
.reply a svg {
  width: 14px;
  height: 14px;
  transition: transform 0.3s ease;
}
.reply a:hover svg {
  transform: translateX(2px);
}

/* Nested Comments */
.children {
  margin-top: 2rem;
  margin-left: 2rem;
  padding-left: 2rem;
  border-left: 3px solid var(--color-yellow);
  position: relative;
}
@media (max-width: 767.98px) {
  .children {
    margin-left: 1rem;
    padding-left: 1rem;
  }
}
.children::before {
  content: "";
  position: absolute;
  left: -6px;
  top: 0;
  width: 9px;
  height: 9px;
  background: var(--color-yellow);
  border-radius: 50%;
  border: 2px solid var(--color-white);
}
.children .comment-body {
  background: rgba(255, 255, 255, 0.95);
  border: 1px solid rgba(216, 177, 80, 0.2);
}

/* Comment Navigation */
.comment-navigation {
  margin: 2rem 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 767.98px) {
  .comment-navigation {
    flex-direction: column;
    gap: 1rem;
  }
}
.comment-navigation .nav-previous a,
.comment-navigation .nav-next a {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.25rem;
  background: var(--color-yellow);
  color: var(--color-dark);
  border-radius: 25px;
  text-decoration: none;
  font-weight: 500;
  transition: all 0.3s ease;
}
.comment-navigation .nav-previous a:hover,
.comment-navigation .nav-next a:hover {
  background: var(--color-dark);
  color: var(--color-white);
  transform: translateY(-2px);
}

/* Comment Form */
.comment-form {
  margin-top: 3rem;
  padding: 2rem;
  background: var(--color-white);
  border-radius: 15px;
}

.comment-respond {
  margin-top: 2.5rem;
}

.comment-reply-title {
  font-size: 1.75rem;
  font-family: var(--font-urbanist);
  color: var(--color-dark);
  margin-bottom: 1.5rem;
  text-align: center;
  position: relative;
}
.comment-reply-title::after {
  content: "";
  position: absolute;
  bottom: -8px;
  left: 50%;
  transform: translateX(-50%);
  width: 50px;
  height: 2px;
  background: var(--color-yellow);
  border-radius: 1px;
}
.comment-reply-title small {
  font-size: 0.9rem;
  margin-left: 1rem;
}

.logged-in-as {
  text-align: center;
  margin-bottom: 1.5rem;
  font-size: 0.9rem;
  color: var(--color-text);
}
.logged-in-as a {
  color: var(--color-yellow);
  text-decoration: none;
  font-weight: 500;
  transition: color 0.3s ease;
}
.logged-in-as a:hover {
  color: var(--color-dark);
}

.required {
  color: #e74c3c;
  font-weight: 700;
}

/* Comment Form Fields */
.comment-form-comment {
  margin-bottom: 1.5rem;
}
.comment-form-comment label {
  display: block;
  font-family: var(--font-urbanist);
  font-weight: 600;
  font-size: 1rem;
  color: var(--color-dark);
  margin-bottom: 0.5rem;
}
.comment-form-comment textarea {
  width: 100%;
  min-height: 150px;
  padding: 1rem 1.25rem;
  border: 2px solid #e1e5e9;
  border-radius: 12px;
  font-size: 1rem;
  font-family: var(--font-plus);
  color: var(--color-dark);
  background: var(--color-white);
  transition: all 0.3s ease;
  outline: none;
  resize: vertical;
  box-sizing: border-box;
  line-height: 1.6;
}
.comment-form-comment textarea:focus {
  border-color: var(--color-yellow);
  background: rgba(216, 177, 80, 0.02);
}
.comment-form-comment textarea:hover:not(:focus) {
  border-color: #c1c8cd;
}

.comment-form-author,
.comment-form-email,
.comment-form-url {
  margin-bottom: 1.5rem;
}
.comment-form-author label,
.comment-form-email label,
.comment-form-url label {
  display: block;
  font-family: var(--font-urbanist);
  font-weight: 600;
  font-size: 1rem;
  color: var(--color-dark);
  margin-bottom: 0.5rem;
}
.comment-form-author input,
.comment-form-email input,
.comment-form-url input {
  width: 100%;
  padding: 1rem 1.25rem;
  border: 2px solid #e1e5e9;
  border-radius: 12px;
  font-size: 1rem;
  font-family: var(--font-plus);
  color: var(--color-dark);
  background: var(--color-white);
  transition: all 0.3s ease;
  outline: none;
  box-sizing: border-box;
}
.comment-form-author input:focus,
.comment-form-email input:focus,
.comment-form-url input:focus {
  border-color: var(--color-yellow);
  background: rgba(216, 177, 80, 0.02);
}
.comment-form-author input:hover:not(:focus),
.comment-form-email input:hover:not(:focus),
.comment-form-url input:hover:not(:focus) {
  border-color: #c1c8cd;
}

.comment-form-cookies-consent {
  margin-bottom: 1.5rem;
  display: flex;
  align-items: center;
  gap: 0.75rem;
}
.comment-form-cookies-consent input[type=checkbox] {
  width: 1.25rem;
  height: 1.25rem;
  accent-color: var(--color-yellow);
}
.comment-form-cookies-consent label {
  font-size: 0.9rem;
  color: var(--color-text);
  line-height: 1.4;
  cursor: pointer;
}

/* Submit Button */
.form-submit {
  text-align: center;
}
.form-submit input[type=submit] {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  min-width: 160px;
  padding: 1rem 1.5rem;
  border: 2px solid var(--color-yellow);
  border-radius: 12px;
  font-family: var(--font-urbanist);
  font-weight: 500;
  font-size: 1rem;
  line-height: 1.5;
  color: var(--color-dark);
  background: var(--color-yellow);
  cursor: pointer;
  transition: all 0.3s ease;
  outline: none;
  text-decoration: none;
  box-sizing: border-box;
}
.form-submit input[type=submit]:hover {
  background: var(--color-dark);
  border-color: var(--color-dark);
  color: var(--color-white);
  transform: translateY(-2px);
}
.form-submit input[type=submit]:active {
  transform: translateY(0);
}

/* No Comments Message */
.no-comments {
  text-align: center;
  padding: 2rem;
  background: var(--color-white);
  border-radius: 15px;
  color: var(--color-text);
  font-size: 1.125rem;
  margin: 2rem 0;
}

/* Comments Closed */
.comments-closed {
  text-align: center;
  padding: 2rem;
  background: rgba(231, 76, 60, 0.1);
  border: 2px solid rgba(231, 76, 60, 0.2);
  border-radius: 15px;
  color: #e74c3c;
  font-weight: 500;
  margin: 2rem 0;
}

/* Responsive Design */
@media (max-width: 767.98px) {
  .comments-area {
    padding: 2rem 0;
  }
  .comment-body {
    padding: 1.5rem;
  }
  .comment-form {
    padding: 1.5rem;
  }
  .children {
    margin-left: 1rem;
    padding-left: 1rem;
  }
}
@media (max-width: 575.98px) {
  .comments-title {
    font-size: 1.75rem;
  }
  .comment-body {
    padding: 1rem;
  }
  .comment-form {
    padding: 1rem;
  }
  .comment-meta {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
  }
  .comment-metadata {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.25rem;
  }
}
/* Sidebar Styles - Bottom Layout */
/* Hide empty sidebar areas */
.widget-area:empty {
  display: none !important;
}

/* Hide sidebar if no widgets have content */
.widget-area .widget:empty {
  display: none;
}

.widget-area {
  margin-top: 4rem;
  padding: 3rem 0;
  background: linear-gradient(135deg, #f8f9fa 0%, #e9ecef 100%);
  border-radius: 20px;
  position: relative;
}
.widget-area::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="grain" width="100" height="100" patternUnits="userSpaceOnUse"><circle cx="50" cy="50" r="1" fill="rgba(0,0,0,0.02)"/></pattern></defs><rect width="100" height="100" fill="url(%23grain)"/></svg>');
  opacity: 0.5;
  border-radius: 20px;
}

.widget-area .container {
  position: relative;
  z-index: 1;
}

/* Widget Grid Layout */
.widget-area {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
