/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}
.slick-list:focus {
  outline: none;
}
.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}
.slick-track:after {
  clear: both;
}
.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
}
[dir=rtl] .slick-slide {
  float: right;
}
.slick-slide img {
  display: block;
}
.slick-slide.slick-loading img {
  display: none;
}
.slick-slide {
  display: none;
}
.slick-slide.dragging img {
  pointer-events: none;
}
.slick-initialized .slick-slide {
  display: block;
}
.slick-loading .slick-slide {
  visibility: hidden;
}
.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

.accordion {
  border-top: 1px solid rgba(0, 0, 0, 0.2);
}

.accordion-item {
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  padding: 1.25rem 0;
}

.accordion-question {
  position: relative;
  width: 100%;
  padding: 0 4.5rem 0 0;
  margin: 0;
  background: 0;
  border: 0;
  font-family: var(--font-urbanist);
  font-weight: 700;
  font-size: 1.5rem;
  line-height: 1.25;
  color: var(--color-dark);
  text-align: start;
  cursor: pointer;
  transition: all 0.25s ease-in-out;
}

.accordion-question:before {
  content: " ";
  position: absolute;
  top: 50%;
  right: 0.5rem;
  width: 1.4375rem;
  height: 0.0625rem;
  background-color: #000;
  border-radius: 2px;
  transform: translateY(-50%);
}

.accordion-question:after {
  content: " ";
  position: absolute;
  top: 50%;
  right: 1.125rem;
  width: 0.0625rem;
  height: 1.4375rem;
  background-color: #000;
  border-radius: 2px;
  transition: all 0.25s ease-in-out;
  transform: translateY(-50%);
}

.accordion-item.active .accordion-question:after {
  opacity: 0;
}

.accordion-answer {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.35s ease;
  padding-right: 4.5rem;
}

.accordion-text {
